import "iview/dist/styles/iview.css";
import { Icon, Spin } from "iview";
export default {
  name: "YQTreeLeaf",
  components: {
    Icon,
    Spin
  },
  props: {
    data: {
      typle: Object,
      required: true
    },
    leafStyle: {
      typle: String,
      default: "step",
      validator: function(value) {
        return ["step", "number"].indexOf(value) !== -1;
      }
    },
    offset: {
      type: Number,
      default: 30
    },
    level: {
      type: Number,
      default: 0
    },
    idKey: {
      type: String,
      default: "id"
    },
    loadData: {
      typle: Function
    },
    leafInfo: {
      typle: Function
    }
  },
  data: function() {
    return {
      leafOffset: 0
    };
  },
  created() {
    if (this.leafStyle === "number") {
      this.leafOffset = 0
    } else {
      this.leafOffset = this.offset
    }
  },
  watch: {},
  computed: {
    isSelected() {
      return "selected" in this.data && this.data.selected;
    },
    shouldShowLeaves() {
      return this.data.expand && this.data.hasChild;
    },
    leafNubmer() {
      let info = this.leafInfo(this.data.yq_tree_index)
      if(info) {
        return info.position.map(element => {
          return (element + 1) + ""
        }).join("-")
      } else {
        return ""
      }
    }
  },
  methods: {
    expandCell() {
      if (this.data.hasChild) {
        if (this.data.children && this.data.children.length) {
          this.dispatch("on-expanded", this.data);
        } else {
          if (this.loadData) {
            this.loadLeaves();
          } else {
            console.error("loadDatahasChild");
          }
        }
      } else {
        console.error("");
      }
    },
    clickCell() {
      if (this.data.hasChild) {
        if (this.data.children && this.data.children.length > 0) {
          this.dispatch("on-selected", this.data);
        } else {
          if (this.loadData) {
            this.loadLeaves();
          } else {
            console.error("loadDatahasChild");
          }
        }
      } else {
        this.dispatch("on-selected", this.data);
      }
    },
    loadLeaves() {
      if (this.data.loading) {
        return;
      }
      this.$set(this.data, "loading", true);
      if (this.loadData) {
        this.loadData(this.data, children => {
          this.$set(this.data, "loading", false);
          this.$set(this.data, "children", children);
          this.dispatch("children-changed", this.data);
        });
      } else {
        console.error("loadDatahasChild");
      }
    },
    dispatch(event, params) {
      let target = "YQTreeSelector";
      let parent = this.$parent;
      let name = parent.$options.name;

      while (parent && name !== target) {
        parent = parent.$parent;
        name = parent.$options.name;
      }
      if (parent) {
        parent.$emit.apply(parent, [event].concat(params));
      }
    }
  }
};
